﻿html {
    // This defines what 1rem is
    font-size: 62.5%; //1 rem = 10px; 10px/16px = 62.5%

    @include respond(tab-land) { // Width > 1200px
        font-size: 56.25%; // 1rem 9px, 9/16 = 56.25%
    }
    @include respond(phone) { // Width < 900px
        font-size: 50%; // 1rem 8px, 8/16 = 50%
    }
    scroll-behavior: smooth;
}

body{
    font-size: 1.6rem;
    font-family:$main-font;
    background-image: url('../img/bg.png');
    background-color: $maincolor-dark;
    &.over{
        overflow: hidden;
    }
}

*{
    font-family: inherit;
    //transition: all .4s ease-in-out;
}

//loader
.loader-box{
    width: 100%;
    height: 100%;
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    right: 0;
    background-color:$maincolor-dark;
    z-index: 99999;
    .loader{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 200px;
        height: 200px;
        .face{
            position: absolute;
            border: 2px solid $color-white;
            
        }
        .face1{
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background-color: $color-grey;
            box-shadow: 0 0 10px rgba(0, 0, 0,1);    
            border-radius: 50%; 
            border-left: 2px solid $color-titel;
            border-top: 2px solid $color-titel;
            .circle{
                transform: rotate(-45deg)
            }
            animation: loader 3s linear reverse infinite;
        }
        .face2{
            top: 30px;
            left: 30px;
            right: 30px;
            bottom: 30px;
            background-color: $color-grey;
            box-shadow: 0 0 10px rgba(0, 0, 0,1);    
            border-radius: 50%; 
            border-right: 2px solid $maincolor-dark;
            border-bottom: 2px solid $maincolor-dark;
            .circle{
                transform: rotate(-45deg)
            }   
            animation: loader 3s linear  infinite;
        }
        .circle{
            position: absolute;
            top: calc(50% - 1px);
            left: 50%;
            width: 50%;
            height: 2px;
            transform-origin: left;
            &::before{
                content: '';
                position: absolute;
                width: 10px;
                height: 10px;
                border-radius: 50%;
                background-color: $maincolor;
                top: -4px;
                right: -6px;
                box-shadow: 0 0 20px $maincolor,
                            0 0 40px $maincolor,
                            0 0 60px $maincolor,
                            0 0 80px $maincolor,
                            0 0 100px $maincolor,
                            0 0 0 5px rgba(#fff,.1);
            }
        }
    }
}

.mix{
    transition: none;
}
.min-titel{
    font-family: $titel-font;
    color: $maincolor;
    position: relative;
    margin-bottom: 6rem;
    h2{
        padding-left: 4.5rem;
        padding-top: .8rem;
        font-size: 2rem;
        text-transform: uppercase;
        font-weight: 600;
        margin: 0;
        &::before{
            content: '';
            position: absolute;
            top: 2rem;
            left: 17rem;
            width: 6rem;
            border-top:.2rem dotted $color-titel; 
        }
        &::after{
            content: '';
            position: absolute;
            top: 1.5rem;
            left: 23rem;
            width: .5rem;
            height: .5rem;
            border-radius: 50%;
            border:.3rem solid $color-titel;
            background-color: $maincolor;
            box-shadow: 0 0 1rem rgba(0, 0, 0, .2);
            box-sizing: content-box;
        }
    }
    .holder{
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 9.5rem;
        height: 4rem;
        border: .1rem dotted $color-titel;
        background: $color-grey;
        border-right: none;
        z-index: -1;
    }
    .num{
        position: absolute;
        left: 25rem;
        bottom: .3rem;
        color: $color-titel;
        font-size: 1.7rem;
        font-weight: 800;

    }
}
::-webkit-scrollbar-thumb {
    border-radius: .8rem;
    background-color: $maincolor;

}
::-webkit-scrollbar{
    background-color: $color-grey;
    width: .3rem;
}
::selection {
    background: $maincolor;
    color: #fff;
}

button{
    cursor: pointer;
    font-size: 1.6rem;
    outline: none !important;
}
h1, h2, h3, h4, h5, h6{
    font-family: $titel-font;
}
p, h2{
    line-height: 1.6;

    @include respond(0){
        line-height: 1.3;
    }
}

ul{
    padding: 0;
    margin: 0;
    list-style: none;
}
a, a:hover, a:focus, a:active {
    color: #fff;
    text-decoration: none;
}

.svg{
    width: 1.5rem;
    height: 1.5rem;
}