.min-btn{
    padding:1.2rem 2.5rem;
    display: inline-block;
    color: #fff;
    background-color: $maincolor;
    margin-right: auto;
    font-size: 1.4rem;
    font-weight: 600;
    letter-spacing: .1rem;
    border-radius: 100px;
    border: .2rem solid transparent;
    box-shadow: 0 0 0 0 rgba($maincolor, 1);
    transition: all .4s ease-in-out;
    &:hover{
        color: $maincolor;
        background-color: $maincolor-dark;  
        text-decoration: none;
        border: .2rem solid $maincolor;
        transform: translateY(-.3rem);
        animation: heart .8s  cubic-bezier(.66, 0, 0, 1);

    }
    &:active{
        transform: translateY(0);
    }

}
// animation
@keyframes heart
{
    to
    {
        box-shadow: 0 0 0 8px rgba($maincolor, 0);
    }
}
@keyframes loader{
    0%{
        transform: rotate(0deg)
    }
    100%{
        transform: rotate(360deg)
    }
}
