header{
    background-image:linear-gradient(rgba($maincolor-dark,.6)),url('../img/header-bg.jpg');
    background-size: cover;
    background-attachment: fixed;
    height: 100vh;
    position: relative;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .navbar{
        padding-top: 1.7rem;
        padding-bottom: 1.7rem;
        transition: all .4s ease-in-out;
        .navbar-brand{
            font-size: 2.2rem;
            font-family: $titel-font;
            font-weight: 800;
            padding: 1.5rem 0;
            letter-spacing: .1rem;
            position: relative;
            span{
                &::after{
                    content: '';
                    width: .5rem;
                    height: .5rem;
                    background-color: $maincolor;
                    position: absolute;
                    bottom: 2.4rem;
                    right: -.8rem;
                }
            }
        }
        .nav-item{
            margin-right: 2.2rem;
            @media only screen and (max-width: 991.98px) { 
                margin-left: auto;
                margin-right: auto;
             }
            letter-spacing: .1rem;
            padding: 1.3rem 0;
            user-select: none;
            a{
                color: #fff !important;
                position: relative;
                transition: all 0.3s linear;
                font-size: 1.4rem;
                font-weight: 600;
                &::before{
                    content: '';
                    display: block;
                    width: 100%;
                    height: .2rem;
                    background-color: $maincolor;
                    position: absolute;
                    bottom: 0;
                    right: 0;
                    transform: scale(0);
                    transition: all 0.3s linear;
                }
                &.active,
                &:hover{
                    color: $maincolor !important;
                    &::before{
                        transform: scale(1);
                    }

                }
            }

        }
    }
    .active-click,
    .scrolled{
        background-color: $maincolor-dark;
        padding-top: .7rem ;
        padding-bottom: .7rem;
        border-bottom: .1rem solid $color-grey;
    }
    .navbar-expand-lg .navbar-nav .nav-link{
        padding-right: 0;
        padding-left: 0;
    }
    /*start heading*/
    .main-heading{
        display: flex;
        flex-direction: column;
        text-align: left;
        .cd-intro{
            margin-bottom: 1rem;
            width: 100% !important;
            h1{
                color: #fff;
                font-weight: 900;
                text-align: left;
                font-size: 5rem;
                letter-spacing: .3rem;
                margin-right: auto;
                b, strong{
                    font-weight: unset;
                }
                @include respond(425){
                    font-size: 4rem;
                    font-weight: 700 !important;


                }
                @include respond(375){
                    font-size: 3.5rem;
                    font-weight: 600 !important;
                    letter-spacing: .2rem;


                }
                @include respond(320){
                    font-size: 3.2rem;
                }
                b{
                    color: $maincolor;
                    margin-left: .7rem;
                }    
            }
            .cd-words-wrapper{
                width:60% !important;

            }
        }
        .skil-box{
            margin-bottom: 3rem;
            ul{
                display: flex;
                padding: 2rem ;
                background-color: rgba($maincolor-dark,.82);
                width: 51.5rem;
                li{
                    color: #fff;
                    font-weight: 600;
                    font-size: 1.5rem;
                    letter-spacing: .2rem;
                    font-family: $titel-font;
                    line-height: 2rem;
                    position: relative;
                    &:not(:last-of-type){
                        margin-right: 2rem;
                        &::after{
                            content: '/';
                            position: absolute;
                            right:-1.5rem;
                            color: $maincolor;
                            @include respond(425){
                                display: none;
                            }
                        }
                    }
                }
                @include respond(425){
                    width: 48rem;
                    li{
                        font-size: 1.4rem;
                        letter-spacing: .2rem;
                        &:not(:last-of-type){
                            margin-right: 1.2rem;
                        }
                    }
                }
                @include respond(375){
                    width:39.1rem;
                    padding: 1.5rem 1rem ;
                    li{
                        font-size: 1.3rem;
                        letter-spacing: .1rem;
                        &:not(:last-of-type){
                            margin-right: 1.2rem;
                        }
    
                    }
                }
                @include respond(320){
                    width: 37rem;
                    padding: 1rem .5rem ;
                    li{
                        font-size: 1.3rem;
                        letter-spacing: .1rem;
                        &:not(:last-of-type){
                            margin-right: .5rem;
                        }
    
                    }
                }
            }
        }
    }
}
/*end header*/
/*start about */
.about{
    margin-top:5rem;
    .min-titel{
        margin-bottom: 3rem !important;
    }
    .img-box{
        margin:0 4rem 4rem 4rem;
        position: relative;
        img{
            display: block;
            max-width: 100%;
            height: auto;
            display: block;
            margin: auto;
            border-radius: .8rem;
        }
        &::after{
            content: '';
            position: absolute;
            top: -3rem;
            left: -3rem;
            height: 20%;
            width: 15%;
            border-radius: .8rem 0;
            border-left: .3rem solid rgba(245, 134, 93, .7);
            border-top: .3rem solid rgba(245, 134, 93, .7);
            transition: all .2s ease-in-out;
        }
        &::before{
            content: '';
            position: absolute;
            display: block;
            border-radius: .8rem;
            z-index: -1;
            height: 90%;
            width: 100%;
            bottom: -1.5rem;
            right: -1.5rem;
            background-image: linear-gradient(to left ,$maincolor 6%,#fff 100%);
            transition: all .2s ease-in-out;
        }
        @media only screen and (max-width: 991.98px) { 
            &::after,
            &::before{
                display: none;
            }

        }
    }
    .contint{
        h3{
            color: #fff;
            font-size: 1.8rem;
            font-weight: 600;
            letter-spacing: .1rem;
            position: relative;
            line-height: 1.5;
            margin-bottom: 3rem;
            span{
                color: $maincolor;
            }
            &::before{
                content: '';
                position: absolute;
                bottom: -2rem;
                left: 0;
                display: block;
                width: 8rem;
                height: .3rem;
                background-color: $maincolor;
            }
        }
        p{
            color: $color-white;
            line-height: 1.8;
            font-weight: 400;
            font-size: 1.4rem;
            padding-top: .5rem;
            padding-bottom: .5rem;
        }
    }
    .personal-info{
        background-color: $color-grey;
        padding:1.5rem 2rem;
        margin-top: 2rem;
        position: relative;
        margin-bottom: 3rem;
        i{
            position: absolute;
            right: 3rem;
            top: 1rem;
            font-size: 4.5rem;
            color: #f9f2f2;
            opacity: .1;
        
        }
        p{
            color: $color-white;
            font-weight: 600;
            margin-left: 1rem;
            position: relative;
            &::after{
                content: '';
                position: absolute;
                top: 1.4rem;
                left: -1.5rem;
                width: .4rem;
                height: .4rem;
                background-color: $maincolor;
                border-radius: 50%;
                display: block;
                border: .3rem solid #36353b;
                box-shadow: 0 0 1rem rgba(0, 0, 0, .2);
                box-sizing: content-box;
            }
            span{
                color: $maincolor;
            }
        }
    }
}
