/*start resume*/
.resume{
    .min-titel{
        h2{
            &::before{
                left:18.5rem;
            }
            &::after{
                left: 24.5rem;
            }
        }
        .num{
            left: 26.5rem;    
        }
    }
    overflow: hidden;
    .resume-contaner{
        width: 100%;
        height: 100%;
        position: relative;
        color: $color-white;
        &::before{
            content: '';
            position: absolute;
            top: 0;
            left: 50%;
            transform: translateX(-50%);
            width: .6rem;
            height: 90%;
            background-color: $color-grey;
            display: block;
        }
        ul{
            display:flex;
            flex-direction: column;
            li{
                width: 100%;
                display:flex;
                position: relative;
                &::before{
                    content: '';
                    display: block;
                    position: absolute;
                    z-index: 10;
                    top: .9rem;
                    left: 50%;
                    margin-left: -1rem;
                    height: 1rem;
                    width: 1rem;
                    border-radius: 50%;
                    border: .5rem solid #36353b;
                    background: $maincolor;
                    box-shadow: 0 0 1rem rgba(0, 0, 0, .2);
                    box-sizing: content-box;
                }
                .resume-box {
                    background-color: $color-grey;
                    width: 45%;
                    text-align: center;
                    position: relative;
                    
                    span{
                        display: block;
                        background-color: $maincolor;
                        padding: 1rem 2rem;
                        color: #fff;
                    }
                    h4{
                        color:$maincolor;
                        font-size: 1.8rem;
                        margin: 0 1.5rem 1.5rem;
                        padding: 1.5rem 0;
                        opacity: .8;
                        font-weight: 400;
                        border-bottom: .1rem solid $color-titel;
                    }
                    p{
                        font-size: 1.4rem;
                        line-height: 1.8;
                        font-weight: 400;
                        text-align: left;
                        padding: 0 2rem 1.5rem;
                        margin: 0;
                    
                    }
                    @include respond(991){
                        width: 100%;
                        margin: 4rem auto auto;
                    }
                }
                
                &:nth-child(odd) .resume-box{
                    &::before{
                        content: '';
                        position: absolute;
                        top: .9rem;
                        right: -2.2rem;
                        display: block;
                        border-top: 1rem solid transparent;
                        border-bottom: 1.2rem solid transparent;
                        border-left: 1rem solid $maincolor  ;
                        border-right: 1.2rem solid transparent;
                        @include respond(991){
                            right: 50%;
                            top: -1.9rem;
                            margin-right: -1.2rem;
                            border-top: 1rem solid transparent;
                            border-bottom: 1.2rem solid $maincolor;
                            border-left: 1rem solid  transparent ;
                            border-right: 1.2rem solid transparent;    
                        }

                    }
                }
                &:nth-child(even) .resume-box{
                    margin-left: auto;
                    &::before{
                        content: '';
                        position: absolute;
                        top: .9rem;
                        left: -2.2rem;
                        display: block;
                        border-top: 1rem solid transparent;
                        border-bottom: 1.2rem solid transparent;
                        border-left: 1rem solid  transparent ;
                        border-right: 1.2rem solid $maincolor;
                        @include respond(991){
                            left: 50%;
                            top: -1.9rem;
                            margin-left: -1rem;
                            border-top: 1rem solid transparent;
                            border-bottom: 1.2rem solid $maincolor;
                            border-left: 1rem solid  transparent ;
                            border-right: 1.2rem solid transparent;    
                        }
                        
                    }
                }
            }
            
        }
    }
}
/*start portfolio*/
.portfolio{
    .min-titel{
        h2{
            &::before{
