            }
            &::after{
                left: 27.5rem;
            }
        }
        .num{
            left: 29.5rem;    
        }
    }
    .fancybox-content{
        transition: none !important;
    }
    .portfolio-container{
        ul{
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            margin-bottom: 4rem;

            li{
                margin: 0 1.4rem 1rem;
                letter-spacing: .15rem;
                padding-bottom: .4rem ;
                color: #fff !important;
                position: relative;
                transition: all 0.3s linear;
                font-size: 1.5rem;
                font-weight: 600;
                cursor: pointer;
                user-select: none;
                &::before{
                    content: '';
                    display: block;
                    width: 100%;
                    height: .2rem;
                    background-color: $maincolor;
                    position: absolute;
                    bottom: 0;
                    right: 0;
                    transform: scale(0);
                    transition: all 0.3s linear;
                }
                &.active,
                &:hover{
                    color: $maincolor !important;
                    &::before{
                        transform: scale(1);
                    }
    
                }
    
            }
        }
        .mix-body{
            overflow: hidden;
            margin-bottom: 3rem;
            position: relative;
            img{
                transition: all .4s ease-in-out;
                display: block;
                max-width: 100%;
                height: auto;
            }
            .overlay{
                position: absolute;
                top: .8rem;
                left: .8rem;
                right: .8rem;
                bottom: .8rem;
                background: rgba(43, 42, 47, 0.95);
                transform: scale(0);
                display: flex;
                flex-direction: column;
                transition: all .4s ease-in-out;
                a{
                    width: 4.2rem;
                    height: 4.2rem;
                    line-height: 4.2rem;
                    background-color: $maincolor-dark;
                    border: .1rem solid $color-grey;
                    color: $maincolor;
                    font-size: 2rem;
                    text-align: center;
                    border-radius: .4rem;
                    display: block;
                    margin: 1rem 2rem;
                    &:first-of-type{
                        margin-top: 2rem;
                    }
                    text-decoration: none;
                
                }
                h4{
                    margin-top: auto;
                    margin-left: auto;
                    letter-spacing: .2rem;
                    font-size: 1.7rem;
                    font-weight: 600;
                    color: $maincolor;
                    padding-right: 2rem;
                    padding-bottom: 2rem;

                }
            }
            &:hover{
                img{
                    transform: scale(1.2);
                }
                .overlay{
                    transform: scale(1);
                
                }
            }
            
        }
    }
}
