                position: relative;
                margin-top: 2rem;
                margin-bottom: 0;
                font-weight: 400;
                text-align: left;
                color:$color-white;
                font-size: 1.4rem;
                &::before,
                &::after{
                    font-family: 'icomoon' !important;
