                border: .1rem solid #434247;
                width: 4.5rem;
                height: 4.5rem;
                background-color: #343338;
                text-align: center;
                border-radius: 50%;
                i{
                    color:$maincolor;
                    font-size: 2rem;
                    line-height: 4.3rem ;
                }
            }
            .titel{
                h4{
                    color: #fff;
                    font-weight: 600;
                    font-size: 1.7rem;
                    line-height: 4rem;
                    text-transform: uppercase;

                }
            }
        }
        .info{
            font-size:1.7rem;
            p{
                margin-bottom: 0;
                line-height: 2rem;
                font-weight: 600;
                color:$color-white;
            }
            @media  (min-width:768px) and (max-width:1024px) {
                font-size: 1.5rem;
            }
        }
        .num-box{
            position: absolute;
            right: -2.4rem;
            top: -2.4rem;
            width: 7.4rem;
            height: 7rem;
            line-height: 7rem;
            background-color: #343338;
            border: .2rem dotted #434247;
            color: rgba(185, 185, 185, .1);
            border-radius: 50%;
            font-weight: 600;
            font-family:$titel-font;
            span{
                position: absolute;
                right: 2.8rem;
                top: .7rem;
                font-size: 2.4rem;
            
            }
        
        }
    }
    form{
        background-image: url('../img/map.png');
        background-size: cover;
        background-position: center;
        margin-top: 2rem;
        .input-box{
            margin-bottom: 4rem;
            input,
            textarea{
                transition: all .4s ease-in-out;
                background-color: transparent;
                color: #fff;
                border: 0;
                border: .1rem solid #5d5c60;
                outline: none;
                box-shadow: none;
                border-radius: 0;
                height: 4.6rem;
                padding: 1.2rem;
                width: 100%;
                display: block;
                font-size: 1.4rem;
                line-height: 1.42857143;
                color: $maincolor;
                &:focus{
                    border-color: $maincolor;
                }
            }
            textarea{
                height: 12rem;
                resize: none;
                padding-top: 1.8rem;
            }
    
        }
    }
}
footer{
    padding: 3rem 0;
    background-color: $color-grey;
    margin-top: 6rem;
    .footer-container{
        color:#fff;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        .footer-brand{
            font-size: 2.2rem;
            font-family: $titel-font;
            font-weight: 800;
            padding: 1rem 0;
            letter-spacing: .1rem;
            position: relative;
            span{
                &::after{
                    content: '';
                    width: .5rem;
                    height: .5rem;
                    background-color: $maincolor;
                    position: absolute;
                    bottom: 1.9rem;
                    right: -.8rem;
                }
            }
        }
        ul{
            display:flex;
            li{
                margin-right: 1.3rem;
                a{
                    display: inline-block;
                    font-size: 1.7rem;
                    width: 3.6rem;
                    height: 3.6rem;
                    line-height: 3.6rem;
                    text-align: center;
                    transition: all .3s;
                    transition: all .4s ease-in-out;
                    &:hover{
                        color: $maincolor;
                        transform: translateY(-.3rem)
                    }
                
                }
            }
        }
        p{
            font-weight: 600;
            letter-spacing: .1rem;
            line-height: 1.8;
            margin-top: 1rem;
            margin-bottom: 0;
            span{
                color: $maincolor;
            }
        }
                
        
    }

}